// Last updated: 2020-05-19 by Eric Yeh
@import "forms-lib";

.character-count {
  @include form-messages;
}

.text-area_global {
  @include text-area_global;
}

.form-control {
  @extend %form-control;
}

.form-group {
  @extend %form-group;
}

.fieldset {
  @extend %fieldset;
}

.disabled-field, .form-group .disabled-field {
  @extend %disabled-field;
}

// superseded by .error-description, retain for backward compatibility
.expDesc {
  @include form-messages;
  color: $error-color;
  display: block;
  width: 100%;
}

// Error Message [start]
// move these to it's own scss file in Style Guide and place it after all form scss files
.error {
 @extend %error;
}

.error-field,
input.error-field,
.form-group input.error-field,
textarea.error-field,
select.error-field {
  border: 1px solid $error-color;
  background-color: rgba(182, 49, 37, 0.05);

  &.-readonly {
    padding: 0 0 12px 0; // 20px between input value to .error-description ul
    border: 0;
    color: $error-color;
    background-color: transparent;
    font-family: $medium-font-family;
  }

  &.select-dropdown {
    background-color: rgba(182, 49, 37, 0.05);
  }
}

.error-description ul {
  @include list-unstyled;
  margin: $extra-tiny-spacing - 2px 0 $extra-tiny-spacing;
  color: $error-color;
  li{
    display: flex;
    .inline-error-icon {
      display: inline-block;
      padding-right:15px;
      margin-right: 10px;
      width: 16px;
      height: 16px + 2px; // align the error icon with label
      color: transparent; // for high contrast mode
      background-color: transparent; // for high contrast mode
      background-image: url($inline-error-icon-path);
      background-repeat: no-repeat;
      background-position: 0 100%; // align the error icon with label
    }
  }

}

.check-box + label + .error-description ul,
.radio-button + label + .error-description ul,
.fieldset + .error-description ul {
  margin-top: 2.5px // 10px - half of check-box or radio-button bottom spacing
}

.error ul, // superseded by .error-description, retain for backward compatibility
ul.error // superseded by .error-description, retain for backward compatibility
{
  margin: $extra-tiny-spacing 0;
  padding-left: $small-spacing;
}
// Error Message [end]