@import "link-lists-lib";
.link-list-title {
  &--small {
    @include link-list-title;
    @include styling(4);
    color: color(dolphin);
  }
  &--large {
    @include link-list-title;
    @include styling(3);
    color: color(inky-blue);
    @include media($mobile) {
      @include styling(2);
    }
  }
}

// .link-list-container wraps the entire list
.link-list {
  @extend %list-unstyled;
  margin-top: 0;
  padding-top: 0;
  &--horizontal {
    @include horizontal-list;
    & li {
      padding-bottom: 12px;
    } 
  }
  & li {
    padding-bottom: $extra-tiny-spacing;
    @include styling(6);
  }
}

/*All styling from link-list to a variation of horizontal link list*/
@mixin link-list--horizontal {
  @extend %list-unstyled;
  @include horizontal-list;
  margin-top: 0;
  padding-top: 0;
  & li {
    padding-bottom: 12px;
    @include styling(6);
  }
}