// for the icon list
@mixin link-icon {
  font-size: px-to-rem(50px);
  line-height: px-to-rem(50px);
  @include media($tablet-max) {
    font-size: px-to-rem(60px);
    line-height: px-to-rem(60px);
  }
}

@mixin link-list-title-dt {
  margin: 0;
  padding-bottom: $small-spacing;
}

@mixin link-list-title {
  margin: 0;
  padding-bottom: $small-spacing;
}

// .link-list-container wraps the entire list
@mixin link-list {
  @extend %list-unstyled;
  margin-top: 0;
  padding-top: 0;
  & li {
    padding-bottom: $extra-tiny-spacing;
    @include styling(6);
  }
}