// mixins
@import "buttons-lib";

// classes
.button {
  & {
    @extend %button;
  }

  //  Theming -- //
  // Extend theming for Default Theme (DS1/DS2)
  @include export-theme-button-classes($button-vars-ds);

  i, img{
    @extend %-button-icon;
  }

  img{
    height: 19px;
    // To fix IE11 issue
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      width: 39px;
    }
    @include media($device) {
      height: 18px;
    }
  }
}