// extracted from carousel pattern 1.1.17 - Eric

@mixin card__container {
  display: block;
  position: relative;
  // padding: 10px 10px 15px 10px; // STYLGD-8140 - build with 0 inset padding
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.04);
  color: black;
  background-color: #fff;
  font-size: $extra-tiny-font-size;
  overflow: hidden;
  text-decoration: none;

  img {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transition: all 0.4s ease;
    width: 100%;
  }

  &:hover img {
    transform: scale(1.04);
  }

  .card__content-category {
    display: block;
  }

  .card__content-title {
    display: block;
  }

  .card__content-body {
    display: block;
  }
  .card__content-action {
    display: block;
  }
}

.card__container {
  @include card__container;
}
