@mixin data-points {
  margin-bottom: 28px;
  @include media($mobile) {
    margin-bottom: 20px;
  }
  label {
    margin-bottom: 4px;
    @include media($mobile) {
      @include styling(9);
    }
  }
  span {
    display: block;
    @include media($mobile) {
      @include styling(7);
    }
  }
}
@mixin phone-number-styling {
  a {
    display: block;
    @include media($mobile) {
      @include styling(7);
    }
  }
}