@import "grunt-webfont/kp-icons";

@mixin font-face {
  font-family: "kp-icons";
  src: url("#{$font-path}kp-icons.eot");
  src: url("#{$font-path}kp-icons.eot?#iefix") format("embedded-opentype"),
    url("#{$font-path}kp-icons.woff") format("woff"),
    url("#{$font-path}kp-icons.ttf") format("truetype"),
    url("#{$font-path}kp-icons.svg#kp-icons") format("svg");
  font-weight: normal;
  font-style: normal;
}