$default-grid-vars: $grid-vars;

@mixin columns-asym($max-num: 4, $col-count: 1 )  {
  grid-column: span $max-num;
  -ms-grid-column-span: $max-num;
  margin-right: 0;
}

@mixin generateColumnsDevice($num-columns, $breakpoint) {
  @for $i from 1 through $num-columns {
    @include columnsDevice($breakpoint, $i);
  }
}

@mixin columnsDevice($breakpoint, $i: 1) {
  .columns-#{$breakpoint}-#{$i} {
    grid-column: span $i;
    -ms-grid-column-span: $i;
    margin-right: 0;
  }
}

@mixin columns($i: 1) {
  grid-column: span $i;
  -ms-grid-column-span: $i;
  margin-right: 0;
}

@mixin generate-offset($num-columns: 12, $i: 1) {
  .offset-#{$i} {
    margin-left: ($i * 100% / $num-columns);
  }
}

@mixin rows($grid-vars: $default-grid-vars) {
  // Set custom conditions for grid-lib-loading
  // DS1 grid has a special condition that needs
  // to run for mobile/ mobile col-3
  $default-grid: true;
  @if $kp-theme-ds2-toggle == true {
    $default-grid: false;
  }
  display: grid;
  grid-template-columns: repeat(setStyle($grid-vars, mobile, num-columns), 1fr);
  @if getStyle($grid-vars, mobile, grid-gap) {
    grid-gap: 0 setStyle($grid-vars, mobile, grid-gap);
  }
  width: 100%;

  // Set default columns for mobile
  @if $default-grid == true {
    @for $i from 1 through setStyle($grid-vars, desktop, num-columns) {
      @if $i <= 4 {
        @if $i == 3 {
          .columns-#{$i},
          .column-#{$i} {
            @include columns-asym(2, 3);
          }
        } @else {
          .columns-#{$i},
          .column-#{$i} {
            @include columns($i);
          }
        }
      } @else {
        .columns-#{$i},
        .column-#{$i} {
          @include columns-asym(setStyle($grid-vars, mobile, num-columns), $i);
        }
      }
    } 
  } @else {
    @for $i from 1 through getStyle($grid-vars, desktop, num-columns) {
      @if $i >= getStyle($grid-vars, mobile, num-columns) {
        .columns-#{$i},
        .column-#{$i} {
          @include columns-asym(setStyle($grid-vars, mobile, num-columns), $i);
        }
      } @else {
        .columns-#{$i},
        .column-#{$i} {
          @include columns($i);
        }
      }
    }
  }

  // set columns per device
  @each $breakpoint, $map in $grid-vars {
    @if getStyle($map, breakpoint-name) != "mobile" and getStyle($map, breakpoint-name) != "tablet-small" {
      @include media(setStyle($map, breakpoint)) {
        grid-template-columns: repeat(setStyle($map, num-columns), 1fr);
        @if getStyle($map, grid-gap) {
          grid-gap: 0 setStyle($map, grid-gap);
        }

        @for $i from 1 through setStyle($map, num-columns) {
          .columns-#{$i} {
            @include columns($i);
          }
        }
      }
    }
  }

  // Generate mobile/desktop breakpoint columns classes EX: columns-mobile-3
  @if $kp-theme-ds2-toggle == true {
    @each $breakpoint, $map in $grid-vars {
      @include media(setStyle($map, breakpoint)) {
        @include generateColumnsDevice(setStyle($map, num-columns), setStyle($map, breakpoint-name));
      }
    }
  }
}

@mixin newGrid__container($grid-vars: $default-grid-vars) {
  @each $breakpoint, $map in $grid-vars {
    @include media(setStyle($map, breakpoint)) {
      margin-right: setStyle($map, margin-horizontal);
      margin-left: setStyle($map, margin-horizontal);
      @if getStyle($map, max-width) {
        max-width: setStyle($map, max-width);
      }
      @if getStyle($map, padding-horizontal) {
        padding-left: setStyle($map, padding-horizontal);
        padding-right: setStyle($map, padding-horizontal);
      }
    }
  }
}
