.inverted-link {
  @extend %inverted-link;
}

.muted {
  color: darken(color(graphite-dark), 20%);
}

// Style system -- Depreciated
.kp-org-styles { 
  h1 { 
    @include styling(1);
    font-family: $base-font-family; 
    @include media($tablet-up) {
      font-family: $light-font-family;
    }
  }
  h2 { 
    @include styling(2);
    font-family: $base-font-family;
    @include media($tablet-up) {
      font-family: $light-font-family;
    }
  }
  h3 { 
    @include styling(3); 
    font-family: $base-font-family;
    @include media($tablet-up) {
      font-family: $light-font-family;
    }
  }
  h4 {
    @include styling(5);
    font-family: $medium-font-family;
    @include media($tablet-up) {
      @include styling(4);
      font-family: $base-font-family;
    }
  }
  h5 {
    font-size: $base-font-size;
    line-height: 24px;
    font-weight: normal; 
    font-family: $medium-font-family;
    @include media($tablet-up) {
      @include styling(5); 
    }
  }
  h6 { 
    @include styling(7); 
    font-family: $medium-font-family;
    @include media($tablet-up) {
      @include styling(6); 
    }
  }
}