@import 'lib/modern/stylesheets/dependencies';
@import "lib/modern/stylesheets/base-lib"; //bourbon-neat mixins
@import "lib/modern/stylesheets/variables";


// 1. SHARED STYLES -- base styles that both themes share:
@import 'lib/modern/stylesheets/main.base'; // at root, accessability and helpers


// Not a loaded default theme
// Print Default responsive classes into .kp-theme-ds1 scoped class
// responsive map is set to DS1/DS2 depending on toggle value
// 2. DS1 scoped responsive classes
.kp-theme-ds1 {
  @import 'lib/modern/stylesheets/main.ds1';

  &.sg-spacing, .sg-spacing { // scope spacing differently
    @import 'lib/modern/stylesheets/spacing-theming';
  }
}


// 3. DS2 scoped responsive classes
.kp-theme-ds2 {
  @import 'lib/modern/stylesheets/main.ds2';
  

  &.sg-spacing, .sg-spacing {
    @import 'lib/modern/stylesheets/spacing-theming';
  }
}