
@import "table-lib";

.data-table {
  @include table-styles;
}

.data-table--large {
  @include table-styles;

  td,
  th {
    padding: $table-padding--large;

    @include media($mobile) {
      padding: $table-padding--small;

      &:last-child {
        padding-bottom: $small-spacing;
      }
    }
  }
}

.data-table--grey-even-row {
  @include data-table--grey-even-row;
}

.data-table--no-border {
  @include data-table--no-border;
}

.-mobile-locked {
  @include media($device) {
    @include table-styles;
    @include -mobile-locked;
  }
}

.hidden-row {
  display: none;
}

.mobile_padding {
  padding: $extra-tiny-spacing;
}

.visuallyhidden {
  @include visually-hidden;

  &--mobile-only {
    @include media($mobile) {
      display: none;
    }
  }
}

.tooltip {
  &.table-tooltip-header {
    display: inline-block;
    position: absolute;
    padding-top: 25px;
    padding-left: 60px;

    @include media($tablet-up) {
      display: none;
    }
  }
}
