@mixin table-arrow-styles {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  margin: 0 0 2px $extra-tiny-spacing;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid;
  border-bottom-color: color(dolphin);
}

@mixin table_sortable--arrows {
  @include table-styles;
  @include table-arrow-styles;
  border-top: 5px solid;
  border-top-color: color(dolphin);
  border-bottom: 0;
  @at-root #{&}-up {
    @include table-arrow-styles;
  }
  @at-root #{&}-down {
    @include table-arrow-styles;
    border-top: 5px solid;
    border-top-color: color(dolphin);
    border-bottom: 0;
  }
}

@mixin children--padding {
  &:first-child {
    padding-left: $micro-spacing;
  }
  &:last-child {
    padding-right: $micro-spacing;
  }
}

@mixin table-styles {
  table-layout: fixed;
  width: 100%;
  text-align: left;

  @include media($mobile) {
    display: block;
  }

  tr, thead {
    p, span {
      margin: 0;
    }
  } 

  caption {
    text-align: left;
    margin-bottom: 28px;
    @include media($mobile) {
      margin-bottom: $small-spacing;
      display: table;
    }
  }
  tr {
    border-bottom: $border--thin;

    &:last-child {
      @include media($tablet-up) {
        border-bottom: $border--thick;
      }
    }

    @include media($mobile) {
      display: block;
      border-bottom: $border--thin;
    }
  }

  thead {
    border-bottom: $border--thick;

    @include media($mobile) {
      border-bottom: $border--thin;
      display: block;
    }
  }
  thead th,
  th[scope~="row"] {
    @include children--padding;
    text-transform: uppercase;
    color: $text-light;
    font-size: $table-label-font-size;
    padding: $table-padding--xl-large;
    text-align: left;

    &.number {
      text-align: right;
    }

    &.th {
      &--header {
        border-bottom: 1px solid color("graphite");
        border-right: 1px solid #fff;
        padding-left: 5px;
      }

      &--light {
        background-color: color("graphite-light");
      }

      &--med {
        background-color: color("graphite");
      }

      &--dark {
        background-color: color("graphite-dark");
      }

      &--darker {
        background-color: color("dolphin");
        color: #fff;
      }
      &--center {
        text-align: center;
      }
    }

    @include media($device) {
      word-wrap: break-word;
    }

    @include media($mobile) {
      display: block;
      position: absolute;
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      width: 1px;
      background-color: black;
      &.number {
        text-align: left;
      }
    }
  }
  tbody {
    @include media($mobile) {
      display: block;
    }
    th,
    td {
      @include children--padding;
      padding: $table-padding--xl-large;
      vertical-align: top;
      word-wrap: break-word;
      font-weight: normal;
      text-align: left;

      &.number {
        text-align: right;
      }

      @include media($mobile) {
        display: block;
        padding: $table-padding--small;

        &:last-child {
          padding-bottom: $small-spacing;
        }

        &.number {
          text-align: left;
        }
      }

      @include media($device) {
        padding-bottom: $extra-tiny-spacing;

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }
      }

      span.table-head {
        @include media($device) {
          color: $text-light;
          text-transform: uppercase;
          font-size: $table-label-font-size;
          width: 100%;
          display: block;
          padding-bottom: $tiny-spacing;
        }
      }
    }
    // legacy support
    th[data-th]:before,
    td[data-th]:before {
      display: none;

      @include media($mobile) {
        content: attr(data-th) " ";
        color: $text-light;
        text-transform: uppercase;
        font-size: $table-label-font-size;
        width: 100%;
        display: block;
        padding-bottom: $tiny-spacing;
      }
    }
  }
  p {
    margin: 0;
  }
}

@mixin data-table--large {
  @include table-styles;
  td,
  th {
    padding: $table-padding--large;
    @include media($mobile) {
      padding: $table-padding--small;
      &:last-child {
        padding-bottom: $small-spacing;
      }
    }
  }
}

@mixin data-table--grey-even-row {
  @include table-styles;
  tr:nth-of-type(even) {
    background: color(graphite-light);
  }
}

@mixin data-table--no-border {
  @include table-styles;
  tr {
    border-bottom: none;
  }
}

@mixin visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

@mixin children--padding {
  &:first-child {
    padding-left: $micro-spacing;
  }

  &:last-child {
    padding-right: $micro-spacing;
  }
}

@mixin -mobile-locked {
  @include table-styles;

  thead {
    @include media($device) {
      border-bottom: $border--thin;
    }
    th {
      @include children--padding;
      @include media($device) {
        position: absolute;
        border: 0;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
      }
      @include media($device) {
        &.number {
          text-align: left;
        }
      }
    }
  }

  tr {
    &:last-child {
      @include media($desktop) {
        border-bottom: $border--thick !important;
      }
    }

    @include media($device) {
      border-top: $border--thin !important;
      border-bottom: $border--thin !important;
    }
  }
  tbody {
    th,
    td {
      @include children--padding;
      @include media($device) {
        display: block;
        padding: $table-padding--small;
        &:last-child {
          padding-bottom: $small-spacing;
        }
        &.number {
          text-align: left;
        }
      }
      @include media($device) {
        padding-bottom: $extra-tiny-spacing;
        &:first-child {
          padding-left: 0;
        }
        &:last-child {
          padding-right: 0;
        }
      }
      span.table-head{
        @include media($device) {
          color: $text-light;
          text-transform: uppercase;
          font-size: $table-label-font-size;
          width: 100%;
          display: block;
          padding-bottom: $tiny-spacing;
        }
      }
    }
    // Legacy support for CSS-handled mobile headings
    td[data-th]:before {
      display: none;
      @include media($device) {
        content: attr(data-th) " ";
        color: $text-light;
        text-transform: uppercase;
        font-size: $table-label-font-size;
        width: 100%;
        display: block;
        padding-bottom: $tiny-spacing;
      }
    }
  }
  .visuallyhidden {
    &--mobile-only {
      @include media($device) {
        display: none;
      }
    }
  }

  &.data-table--large {

    td,
    th {
      @include media($device) {
        padding: $table-padding--small;

        &:last-child {
          padding-bottom: $small-spacing;
        }
      }
    }
  }

  .tooltip {
    &.table-tooltip-header {
      display: inline-block;
      position: absolute;
      padding-top: 25px;
      padding-left: 60px;

      @include media($desktop) {
        display: none;
      }
    }
  }

  &.mobile-sort {
    display: none;

    @include media($device) {
      display: block;
      max-width: 100%;
    }
  }
}
@mixin data-table-sg-spacing {
  margin: 0 0 $small-spacing 0;
  @include media($tablet-up) {
    margin: 0 0 32px 0;
  }
}
