@mixin scrollbox {
  box-sizing: border-box;
  padding: 0 0 $medium-spacing 0;
  border: solid 1px color("graphite");
  .scrollbox_content {
    @include size(auto);
    min-height: 200px;
    max-height: 600px;
    overflow-y: scroll;
    padding: $medium-spacing;
    color: color("dolphin");
    -ms-scrollbar-base-color: color("graphite-dark");
    -ms-scrollbar-3dlight-color: color("graphite-dark");
    -ms-scrollbar-highlight-color: color("graphite-dark");
    -ms-scrollbar-track-color: color("pebble");
    -ms-scrollbar-arrow-color: color("pebble");
    -ms-scrollbar-shadow-color: color("graphite-dark");
    -ms-scrollbar-darkshadow-color: color("graphite-dark");
    *:first-child { 
      margin-top: 0px; 
    }
    @at-root #{&}::-webkit-scrollbar {
      @include media ($desktop) {
        background: color("pebble");
        border: solid color("pebble") 2px;
        margin: 40px;
      }
      @at-root #{&}-thumb {
        @include media ($desktop) {
          -webkit-border-radius: 20px;
          border-radius: 20px;
          border: solid color("pebble") 3px;
          background: color("graphite-dark");
          padding: 10px;
        }
      }
    }
    @media print{
      overflow: visible;
      max-height: 100%;
      border: none;
    }
  }
}