@mixin star-styles($size) {
  font-family: 'kp-icons';
  display: inline-block;
  margin: 0;
  line-height: 1;
  font-size: $size;
  letter-spacing: 2px;
  color: color('kp-blue');
}

$star-rating-array: ('0_5': $star-rating-half + $star-rating-empty + $star-rating-empty + $star-rating-empty + $star-rating-empty,
  '1': $star-rating + $star-rating-empty + $star-rating-empty + $star-rating-empty + $star-rating-empty,
  '1_5': $star-rating + $star-rating-half + $star-rating-empty + $star-rating-empty + $star-rating-empty,
  '2': $star-rating + $star-rating + $star-rating-empty + $star-rating-empty + $star-rating-empty,
  '2_5': $star-rating + $star-rating + $star-rating-half + $star-rating-empty + $star-rating-empty,
  '3': $star-rating + $star-rating + $star-rating + $star-rating-empty + $star-rating-empty,
  '3_5': $star-rating + $star-rating + $star-rating + $star-rating-half + $star-rating-empty,
  '4': $star-rating + $star-rating + $star-rating + $star-rating + $star-rating-empty,
  '4_5': $star-rating + $star-rating + $star-rating + $star-rating + $star-rating-half,
  '5': $star-rating + $star-rating + $star-rating + $star-rating + $star-rating,
  'blank': $star-rating-empty + $star-rating-empty + $star-rating-empty + $star-rating-empty + $star-rating-empty,
);

@each $name,
  $star-rating-value in $star-rating-array {
  %star-rating_#{$name} {
    margin: $extra-tiny-spacing;

    @at-root #{&}-large {
      &:before {
        content: $star-rating-value;
        @include star-styles(32px);
      }
    }

    @at-root #{&}-small {
      &:before {
        content: $star-rating-value;
        @include star-styles(0.65rem);
      }
    }
    @at-root #{&}:before {
      content: $star-rating-value;
      @include star-styles(20px);
    }
  }
}

%star-rating_text {
  @at-root #{&}-inline {
    @include styling(3);
    font-size: $large-font-size !important;
    display: inline-block;
    padding: $extra-tiny-spacing;
    margin: 0;
  }

  @at-root #{&}-inline_large {
    @include styling(3);
    font-size: $large-font-size !important;
    display: inline-block;
    padding: $extra-tiny-spacing;
    margin: 0;
  }

  @at-root #{&}-summary {
    @include styling(4);
    font-size: $medium-font-size !important;
    display: inline-block;
  }
}
