
// Theming namespace
$kp-ds-themes: (
  theme1: kp-theme-ds1,
  theme2: kp-theme-ds2
);

// Toggle theme (ds1 set to default)
$kp-theme-ds2-toggle: false !default;

$current-theme: null;
@if ($kp-theme-ds2-toggle == true) {
  $current-theme: 'ds2';
} @else {
  $current-theme: 'ds1';
}

.current-theme {
  content: $current-theme;
}
