@import "../variables/spacing.var.ds2";
// copied from wpmmr-front-door - Eric 2020-12-11

// DS2 Unit spacing
//
// example usage:
// .element {
//    @include ds2-3u('margin-bottom');
// }

@mixin ds2-spacing-1u($property) {
  #{$property}: $ds2-1u-mobile;
  @include media($tablet) { #{$property}: $ds2-1u-tablet; }
  @include media($desktop) { #{$property}: $ds2-1u-desktop; }
}

@mixin ds2-spacing-2u($property) {
  #{$property}: $ds2-2u-mobile;
  @include media($tablet) { #{$property}: $ds2-2u-tablet; }
  @include media($desktop) { #{$property}: $ds2-2u-desktop; }
}

@mixin ds2-spacing-3u($property) {
  #{$property}: $ds2-3u-mobile;
  @include media($tablet) { #{$property}: $ds2-3u-tablet; }
  @include media($desktop) { #{$property}: $ds2-3u-desktop; }
}

@mixin ds2-spacing-4u($property) {
  #{$property}: $ds2-4u-mobile;
  @include media($tablet) { #{$property}: $ds2-4u-tablet; }
  @include media($desktop) { #{$property}: $ds2-4u-desktop; }
}

@mixin ds2-spacing-5u($property) {
  #{$property}: $ds2-scaling-5u-mobile;
  @include media($tablet) { #{$property}: $ds2-5u-tablet; }
  @include media($desktop) { #{$property}: $ds2-5u-desktop; }
}

@mixin ds2-spacing-6u($property) {
  #{$property}: $ds2-scaling-6u-mobile;
  @include media($tablet) { #{$property}: $ds2-6u-tablet; }
