}

@mixin ds2-spacing-7u($property) {
  #{$property}: $ds2-scaling-7u-mobile;
  @include media($tablet) { #{$property}: $ds2-7u-tablet; }
  @include media($desktop) { #{$property}: $ds2-7u-desktop; }
}

@mixin ds2-spacing-5ux2($property) {
  #{$property}: $ds2-5ux2-mobile;
  @include media($tablet) { #{$property}: $ds2-5ux2-tablet; }
  @include media($desktop) { #{$property}: $ds2-5ux2-desktop; }
}


