$spacing-vars-ds2: (
  spacings: (
    1: (
      vertical-spacing: $ds2-1u-mobile,
      tablet: (
        vertical-spacing: $ds2-1u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-1u-desktop
      )
    ),
    2: (
      vertical-spacing: $ds2-2u-mobile,
      tablet: (
        vertical-spacing: $ds2-2u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-2u-desktop
      )
    ),
    3: (
      vertical-spacing: $ds2-3u-mobile,
      tablet: (
        vertical-spacing: $ds2-3u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-3u-desktop
      )
    ),
    4: (
      vertical-spacing: $ds2-4u-mobile,
      tablet: (
        vertical-spacing: $ds2-4u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-4u-desktop
      )
    ),
    5: (
      vertical-spacing: $ds2-5u-mobile,
      tablet: (
        vertical-spacing: $ds2-5u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-5u-desktop
      )
    ),
    6: (
      vertical-spacing: $ds2-6u-mobile,
      tablet: (
        vertical-spacing: $ds2-6u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-6u-desktop
      )
    ),
    7: (
      vertical-spacing: $ds2-7u-mobile,
      tablet: (
        vertical-spacing: $ds2-7u-tablet
      ),
      desktop: (
        vertical-spacing: $ds2-7u-desktop
      )
    )
  )
);

@mixin print-vertical-spacing-classes {
  @each $key, $value in getStyle($spacing-vars-ds2, spacings) {
    .spacing-#{$key}u {
      margin: getStyle($value, vertical-spacing) 0;
      @include media($tablet) {
        margin: getStyle($value, tablet, vertical-spacing) 0;
      }
      @include media($desktop) {
        margin: getStyle($value, desktop, vertical-spacing) 0;
      }
    }
  }
}