/* stylelint-disable declaration-no-important */

@mixin print-link {
  display: none;
  @include media($tablet-up) {
    display: inline;
  }
}

@mixin _print($feature) {
  @if $add-print-styles == true {
    .print-only, .print-only-inline, .print-only-inline-block {
      display: none !important;
    }
    @include print-only;
  }
}

@mixin print-only{
  @media print {
    * {
      -webkit-print-color-adjust: exact !important;
      color-adjust: exact !important;
    }

    body {
      color: $text-dark !important;
      background: #fff !important;
      a, a:visited {
        color: $text-dark !important;
      }
    }
  
    .print-only {
      display: block !important;
    }
  
    .print-only-inline {
      display: inline !important;
    }
  
    .print-only-inline-block {
      display: inline-block !important;
    }
  
    .print-only-logo {
      display: block;
      width: 33%;
    }
  
    .screen-only, .kp-global-header-component, .kp-global-footer-component {
      display: none !important;
    }
  
    .kp-body-component {
      margin-top: 28pt;
      padding-top: 28pt;
      border-top: 1pt solid color(text-dark);
    }
  }
}

