// _colors.scss
//
// Color variables and background classes for all KP
// color values. Only include base colors in this file,
// not aliases.
//
// NOTE: if you want a variable here to be added to the docs
// please add it to the list in add_colors.js
@import 'variables/colors.vars';

$colors: (
  // This section contains YML colors
  "blue-light": $blue-light,
  "blue-extra-dark": $blue-extra-dark,
  "black": $black,
  "gray-dark": $gray-dark,
  "gray": $gray,
  "green-regular": $green-regular,
  "red": $red,
  "transparent-black-light": $transparent-black-light,
  "transparent-black-regular": $transparent-black-regular,
  "transparent-black-dark": $transparent-black-dark,

  // This section contains KP Style Guide colors
  // used only for existing AEM components
  "interactive-blue": $interactive-blue,
  "contrast-blue": $contrast-blue,
  "interactive-blue-dark": $interactive-blue-dark,
  "kp-blue": $kp-blue,
  "inky-blue": $inky-blue,
  "dolphin": $dolphin,
  "graphite-dark": $graphite-dark,
  "graphite": $graphite,
  "graphite-light": $graphite-light,
  "pebble": $pebble,
  "midnight": $midnight,
  "sky": $sky,
  "moss": $moss,
  "grass": $grass,
  "leaf": $leaf,
  "burnt-sienna": $burnt-sienna,
  "tiger-lily": $tiger-lily,
  "tangerine": $tangerine,
  "ocean": $ocean,
  "aqua": $aqua,
  "robin-egg": $robin-egg,
  "acai": $acai,
  "fuchsia": $fuchsia,
  "rose": $rose,
  "plum": $plum,
  "lilac": $lilac,
  "lavender": $lavender,
  "deep-red": $deep-red,
  "deep-chalk": $deep-chalk,
  "mid-chalk": $mid-chalk,
  "light-chalk": $light-chalk,
  "mid-aqua": $mid-aqua,
  "light-aqua": $light-aqua,
  "pale-blue": $pale-blue,
  "motion-blue": $motion-blue,
  "white": $white,
  "dolphin-light": $dolphin-light,
  "slate": $slate
);

@function color($key){
  @return map-get($colors, $key);
}

@function set-text-color($bgcolor) {
  @if (lightness($bgcolor) > 50) {
    @return #000000;
  } @else {
    @return #ffffff;
  }
}
