$ds2-button__font-size--device: 13px;
$ds2-button__font-size--mobile: 13px;
$ds2-button__font-size--tablet: 16px;
$ds2-button__font-size--desktop: 18px;

$ds2-button__ln--mobile: 20px;
$ds2-button__ln--tablet: 24px;
$ds2-button__ln--desktop: 24px;

$ds2-button__large-font-size: 24px;
$ds2-button__large-line-height: 20px;

$ds2-button__text-color--default: $interactive-blue-dark;
$ds2-button__background-color--default: $pale-blue;
$ds2-button__border-color--default: $interactive-blue;
$ds2-button__hover-text-color--default: white;
$ds2-button__hover-background-color--default: $interactive-blue;
$ds2-button__hover-border-color--default: $interactive-blue;
$ds2-button__active-text-color--default: white;
$ds2-button__active-background-color--default: $interactive-blue-dark;
$ds2-button__active-border-color--default: $interactive-blue-dark;
$ds2-button__border-radius: 100px;
$ds2-button__icon-svg-filter--default: grayscale(100) invert(1) brightness(100);

$ds2-button__text-color--inverted: $interactive-blue-dark;
$ds2-button__background-color--inverted: white;
$ds2-button__border-color--inverted: $interactive-blue;

$ds2-button__text-color--disabled: $gray-dark;
$ds2-button__background-color--disabled: transparent;
$ds2-button__border-color--disabled: transparent;
$ds2-button__icon-svg-filter--disabled: grayscale(100) opacity(80%);

$ds2-button__icon--size: 24px;
$ds2-button__icon--position-vertical: 50%;
$ds2-button__icon--position-vertical-offset: -12px; // half of $ds2-button__icon--size
$ds2-button__icon--position-horizontal: 10px;
$ds2-button__padding-vertical--mobile: 7px;
$ds2-button__padding-horizontal--mobile: 10px;
$ds2-button__padding-vertical--tablet: 9px;
$ds2-button__padding-horizontal--tablet: 13px;
$ds2-button__padding-vertical--desktop: 13px;
$ds2-button__padding-horizontal--desktop: 15px;
$ds2-button__padding-horizontal--icon-side: 40px;

$ds2-button-hover-color: color("blue-regular");
$ds2-button-disable-color: color("blue-regular");
$ds2-button-inverted-disable-color: #ffffff;

// Icons
$ds2-button__icon--size: 24px;
$ds2-button__icon--position-vertical: 50%;
$ds2-button__icon--position-vertical-offset: -12px; // half of $button__icon--size
$ds2-button__icon-svg-filter--default: grayscale(100) invert(1) brightness(100);
$ds2-button__padding-horizontal--icon-side: 40px;