// Last updated on 2019-06-13 by Eric Yeh for SG v3.9.17

@import "checkboxes-lib";

.sg-spacing .fieldset {
  @include checkbox-sg-spacing;
}

.check-box {
  @include check-box-class();

  &.-variation-1 {
    @include check-box-class(
      $check-box-font-variation-1,
      $check-box-size-variation-1,
      $check-box-checkmark-size-variation-1,
      $check-box-checkmark-position-top-variation-1,
      $check-box-checkmark-position-left-variation-1,
      $check-box-space-right-variation-1,
      $check-box-space-bottom-variation-1
    );
  }

  &.-variation-2 {
    @include check-box-class(
      $check-box-font-variation-2,
      $check-box-size-variation-2,
      $check-box-checkmark-size-variation-2,
      $check-box-checkmark-position-top-variation-2,
      $check-box-checkmark-position-left-variation-2,
      $check-box-space-right-variation-2,
      $check-box-space-bottom-variation-2
    );
  }

  &.-variation-3 {
    @include check-box-class(
      $check-box-font-variation-3,
      $check-box-size-variation-3,
      $check-box-checkmark-size-variation-3,
      $check-box-checkmark-position-top-variation-3,
      $check-box-checkmark-position-left-variation-3,
      $check-box-space-right-variation-3,
      $check-box-space-bottom-variation-3
    );
  }


  // Typography styling($i) override - force consistent checkmark positioning even when typography styling is applied to label
  @include media($device) {
    body:not(.-locked) .check-box + label:not(.-locked) {
      line-height: 1em;
    }
    @include media($mobile) {
      body:not(.-locked) .styling-1:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-2:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-3:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-4:not(.-locked) {
        font-size: 16px;
      }
    }
    @include media($tablet-up) {
      body:not(.-locked) .styling-1:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-2:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-3:not(.-locked) {
        font-size: 16px;
      }
      body:not(.-locked) .styling-4:not(.-locked) {
        font-size: 16px;
      }
    }
  }
}

.checkbox-toggle-switch {
  @include checkbox-toggle-switch;
}


// Default INPUT type="checkbox" styles (should not be used).
// These styles are created for the sole purpose of neutralize the issues caused by the incorrect use of checkbox mixin within kp-foundation.
// Specifically, the use of the line @include checkbox($checkbox-size-mobile); in _global-styles.scss without a specified css class selector.
// To utilize the StyleGuide styled checkbox, please use class="check-box" for all INPUT type="checkbox" PLUS the addition of <span aria-hidden="true"> inside its matching LABEL in your project.
input[type="checkbox"]:not(.check-box):not(.checkbox-toggle-switch) {
  position: absolute;
  display: inline-block;
  margin: 12px 0 0 2px;
  width: 20px;
  height: 20px;
  border: $check-box-border-width solid $check-box-default-color;
  //Un-do browser specific CSS extensions set in _forms.scss
  -moz-appearance: none;
  -webkit-appearance: none;

  & + label {
    display: inline-block;
    margin: 10px 0 0;
    padding-top: 2px;
    padding-left: 40px;
    font-size: $base-font-size;
  }
}

input[type="checkbox"]:checked:not(.check-box):not(.checkbox-toggle-switch) {
  //Un-do browser specific CSS extensions set in _forms.scss
  -moz-appearance: checkbox;
  -webkit-appearance: checkbox;
}

input[type="checkbox"]:focus:not(.check-box):not(.checkbox-toggle-switch) {
  @include checkbox-focus-outline;
}

input[type="checkbox"]:disabled:not(.check-box):not(.checkbox-toggle-switch) {
  border-color: $check-box-disable-color;
}
