// extracted from _document-lib.scss - Eric 2020-12-11
@mixin button-sg-spacing {
  margin-bottom: $tiny-spacing + 1;
  @include media($tablet-up) {
    margin-bottom: $small-spacing;
  }
}

@mixin data-table-sg-spacing {
  margin: 0 0 $small-spacing 0;
  @include media($tablet-up) {
    margin: 0 0 32px 0;
  }
}

%form-group-sg-spacing {
  margin-bottom: $small-spacing;
}

@mixin form-group-sg-spacing {
  @extend %form-group-sg-spacing;
}

@mixin paragraph-sg-spacing {
  margin: 28px 0;
}

@mixin h1-3-heading-sg-spacing {
  margin: 28px 0;
}

@mixin h4-6-heading-sg-spacing {
  margin: $small-spacing 0;
}