@import "print-lib";
/* stylelint-disable declaration-no-important */
.print-only, .print-only-inline, .print-only-inline-block {
  display: none !important;
}

.print-link {
  display: none;
  background: none;
  border: 0;
  padding: 0;  
  color: $button-color;
  &:hover {
    color: $button-hover-color;
    outline: 0; 
  }
  &:focus {
    color: $button-hover-color;  
  }  
  i {
    margin-right: $extra-tiny-spacing;
  }
}

@include media($tablet-up) {
  .print-link {
    display: inline;
  }
}

@include print-only;
/* stylelint-enable */
