// Import DS1 Spacing
@import "mixins/spacing.mixin.ds1";
@import "mixins/spacing.mixin.ds2";



// Use spacing value for base margin values
// SPACING -- DS2
// spacing changes based on theme
@if $kp-theme-ds2-toggle == false {
  table[class*="data-table"] {
    margin: 0 0 32px 0;
  }
  .fieldset {
    margin-bottom: $small-spacing;
  }
  .form-group {
    @include form-group-sg-spacing;
  }
}

p {
  @if $kp-theme-ds2-toggle == true {
    @include ds2-spacing-2u("margin-top");
    @include ds2-spacing-2u("margin-bottom");
    margin-left: 0;
    margin-right: 0;
  } @else {
    @include paragraph-sg-spacing;
  }
}

.button {
  @include button-sg-spacing;
}

h1,
h2,
h3 {
  @if $kp-theme-ds2-toggle == true {
    @include ds2-spacing-2u("margin-top");
    @include ds2-spacing-2u("margin-bottom");
    margin-left: 0;
    margin-right: 0;
  } @else {
    @include h1-3-heading-sg-spacing;
  }
}

h4,
h5,
h6 {
  @if $kp-theme-ds2-toggle == true {
    @include ds2-spacing-1u("margin-top");
    @include ds2-spacing-1u("margin-bottom");
    margin-left: 0;
    margin-right: 0;
  } @else {
    @include h4-6-heading-sg-spacing;
  }
}
