@mixin radio-default-outline {
  -webkit-box-shadow: 0 0 0 2px $radio-button-default-color;
  -moz-box-shadow: 0 0 0 2px $radio-button-default-color;
  box-shadow: 0 0 0 2px $radio-button-default-color;
}

@mixin radio-focus-outline {
  @include firefox-styling;
  -webkit-box-shadow: 0 0 0 2pt $radio-button-focus-color;
  -moz-box-shadow: 0 0 0 2pt $radio-button-focus-color;
  box-shadow: 0 0 0 2pt $radio-button-focus-color;
  @include ie-styling;
  @-moz-document url-prefix() {
    outline: 1px dotted $check-box-disable-color;
  }
}

@mixin radio-disabled-outline {
  -webkit-box-shadow: 0 0 0 2px $radio-button-disable-color;
  -moz-box-shadow: 0 0 0 2px $radio-button-disable-color;
  box-shadow: 0 0 0 2px $radio-button-disable-color;
}

// radio facade appearance in default unselected state
@mixin radio-button-default {
  border-radius: 50%;
  @include radio-default-outline;
}

// radio facade appearance in unselected focus state
@mixin radio-button-default-focus {
  border: 1pt solid $radio-button-default-color;
  @include radio-focus-outline;
}

// radio facade appearance in selected state
@mixin radio-button-checked ($radio-button-border-size) {
  border: $radio-button-border-size solid #fff;
  background-color: $radio-button-default-color;
  @include radio-default-outline;
}

// radio facade appearance in selected focus state
@mixin radio-button-checked-focus ($radio-button-border-size) {
  border: $radio-button-border-size solid #fff;
  background-color: $radio-button-default-color;
  @include radio-focus-outline;
}

// radio facade appearance in disabled state
@mixin radio-button-disabled {
  background-color: $radio-button-disable-color;
  @include radio-disabled-outline;
}

@mixin radio-button($radio-button-font: $radio-button-font-variation-1, $radio-button-size: $radio-button-size-variation-1, $radio-button-position: $radio-button-position-variation-1, $radio-button-space-right: $radio-button-space-right-variation-1, $radio-button-space-bottom: $radio-button-space-bottom-variation-1) {

  // hide the real radio button
  position: absolute;
  margin-top: $radio-button-space-bottom/2;
  opacity: 0;

  & + label {
    display: inline-block;
    position: relative;
    margin: $radio-button-space-bottom/2 0;
    padding-left: $radio-button-size + $radio-button-space-right;
    min-width: $radio-button-size;
    font-size: $radio-button-font;
    color: $text-dark;

    // add radio facade
    &:before {
      content: '';
      position: absolute;
      top: $radio-button-position;
      left: 2px;
      width: $radio-button-size;
      height: $radio-button-size;
      @include radio-button-default;
    }
  }

  &:active + label:before {
    @include radio-button-checked-focus($radio-button-position);
  }

  &[type="radio"]:not(:checked):focus + label:before {
    @include radio-button-default-focus;
  }

  &[type="radio"]:checked + label:before {
    @include radio-button-checked($radio-button-position);
  }

  &[type="radio"]:checked:focus + label:before {
    @include radio-button-checked-focus($radio-button-position);
  }
  
  &[type="radio"]:disabled + label {
    color: $radio-button-disable-text;
    cursor: not-allowed;
  }

  &[type="radio"]:disabled + label:before {
    @include radio-disabled-outline;
  }

  &[type="radio"]:disabled:active + label:before {
    background-color: transparent;
  }

  &[type="radio"]:disabled:checked + label:before {
    @include radio-button-disabled;
  }
}
