// Last updated on 2019-06-20 by Eric Yeh for SG v3.9.19

@mixin checkbox-focus-outline {
  border: $check-box-border-width solid $check-box-default-color;
  @include firefox-styling;
  -webkit-box-shadow: 0 0 0 2pt $check-box-focus-color;
  -moz-box-shadow: 0 0 0 2pt $check-box-focus-color;
  box-shadow: 0 0 0 2pt $check-box-focus-color;
  @include ie-styling;
  outline: 1px dotted $check-box-disable-color;
}

// ADA compliant checkbox
// Note: "@mixin check-box" AND "@mixin check-box-class" contains the same code logic as of 2019-06-01. "@mixin check-box-class" is created temporarily in order to reconcile the issues encountered when merging "@import checkboxes-lib" in checkboxes.scss
@mixin check-box(
  $check-box-font: $check-box-font-variation-1,
  $check-box-size: $check-box-size-variation-1,
  $check-box-checkmark-size: $check-box-checkmark-size-variation-1,
  $check-box-checkmark-position-top: $check-box-checkmark-position-top-variation-1,
  $check-box-checkmark-position-left: $check-box-checkmark-position-left-variation-1,
  $check-box-space-right: $check-box-space-right-variation-1,
  $check-box-space-bottom: $check-box-space-bottom-variation-1
) {
  // hide the real checkbox
  position: absolute;
  margin-top: $check-box-space-bottom/2;
  opacity: 0;

  + label {
    display: inline-block;
    position: relative;
    margin: $check-box-space-bottom/2 0;
    padding: $check-box-border-width 0 0 $check-box-size + $check-box-space-right - $check-box-border-width;
    min-height: $check-box-size;
    font-size: $check-box-font;
    line-height: 1em;
    color: $text-dark;

    // empty-box of the checkbox facade
    span[aria-hidden="true"]:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: $check-box-size;
      height: $check-box-size;
      border: $check-box-border-width solid $check-box-default-color;
    }
  }

  &:focus + label span[aria-hidden="true"]:before {
    @include checkbox-focus-outline;
  }

  &[type="checkbox"]:checked + label {
    span[aria-hidden="true"]:before {
      background-color: $check-box-default-color;
    }

    // check mark of the checkbox facade
    span[aria-hidden="true"]:after {
      @include icon-for("checkmark");
      position: absolute;
      top: $check-box-checkmark-position-top;
      left: $check-box-checkmark-position-left;
      font-size: $check-box-checkmark-size;
      color: #fff;
    }
  }

  &[type="checkbox"]:disabled + label {
    color: $check-box-disable-text;
    cursor: not-allowed;

    span[aria-hidden="true"]:before {
      border-color: $check-box-disable-color;
    }
  }

  &[type="checkbox"]:disabled:checked + label span[aria-hidden="true"]:before {
    background-color: $check-box-disable-color;
  }
}

@mixin checkbox-toggle-switch-focus-outline {
  @include firefox-styling;
  -webkit-box-shadow: 0 0 0 2pt $check-box-focus-color;
  -moz-box-shadow: 0 0 0 2pt $check-box-focus-color;
  box-shadow: 0 0 0 2pt $check-box-focus-color;
  @include ie-styling;
  outline: 1px dotted $check-box-disable-color;
}

@mixin checkbox-toggle-switch {
  // hide the real checkbox
  position: absolute;
  opacity: 0;

  + label {
    display: inline-block;
    position: relative;
    padding: 4px 0 0 37px + 10px;

    // toggle switch off state
    span[aria-hidden="true"] {
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      width: 37px;
      height: 26px;
      border-radius: 19px;
      background: $checkbox-toggle-switch-color-off;
    }

    span[aria-hidden="true"]:before {
      content: '';
      position: absolute;
      top: 1px;
      left: 1px;
      width: 24px;
      height: 24px;
      border-radius: 100%;
      background: #fff;
      transition: left $checkbox-toggle-switch-transition-speed ease-out;
    }

    span[aria-hidden="true"]:after {
      @include icon-for("checkmark");
      position: absolute;
      top: 6px;
      left: 19px;
      font-size: 11px;
      color: transparent;
    }
  }

  &:focus + label span[aria-hidden="true"] {
    @include checkbox-toggle-switch-focus-outline;
  }

  // toggle switch on state
  &[type="checkbox"]:checked + label {

    span[aria-hidden="true"] {
      background: $checkbox-toggle-switch-color-on;
    }
    
    span[aria-hidden="true"]:before {
      left: 12px;
      transition: left $checkbox-toggle-switch-transition-speed ease-out;
    }

    span[aria-hidden="true"]:after {
      color: $checkbox-toggle-switch-color-on;
      transition: color $checkbox-toggle-switch-transition-speed ease-out;
    }
  }
}
//sg spacing 
@mixin checkbox-sg-spacing {
  margin-bottom: $small-spacing;
}

// ADA compliant checkbox
// Note: "@mixin check-box" AND "@mixin check-box-class" contains the same code logic as of 2019-06-01. "@mixin check-box-class" is created temporarily in order to reconcile the issues encountered when merging "@import checkboxes-lib" in checkboxes.scss
@mixin check-box-class($check-box-font: $check-box-font-variation-1, $check-box-size: $check-box-size-variation-1, $check-box-checkmark-size: $check-box-checkmark-size-variation-1, $check-box-checkmark-position-top: $check-box-checkmark-position-top-variation-1, $check-box-checkmark-position-left: $check-box-checkmark-position-left-variation-1, $check-box-space-right: $check-box-space-right-variation-1, $check-box-space-bottom: $check-box-space-bottom-variation-1) {

  @include check-box(
    $check-box-font,
    $check-box-size,
    $check-box-checkmark-size,
    $check-box-checkmark-position-top,
    $check-box-checkmark-position-left,
    $check-box-space-right,
    $check-box-space-bottom
  );
  // updated on 2018-11-02 by Nancy
  // Needed to have braced scoped to checkbox class
  @at-root #{&}:focus + label span[aria-hidden="true"]:before {
    @include checkbox-focus-outline;
  }
}
