@mixin print-styling-classes($id, $map) {
  .styling-#{$id},
  .styling-#{$id}-marketing {
    @include styling-x($map);
  }
}

@mixin print-alias-classes($id, $map) {
  @if getStyle($map, alias) != null {
    .#{getStyle($map, alias)} {
      @include styling-x($map);
    }
  }
}

@mixin print-h-tags($heading-parent-map) {
  @each $id, $map in $heading-parent-map {
    h#{$id} {
      font-size: setStyle($map, font-size);
      font-family: setStyle($map, font-family);
      @if getStyle($map, line-height) {
        line-height: setStyle($map, line-height);
      }
      @if getStyle($map, letter-spacing) {
        letter-spacing: setStyle($map, letter-spacing);
      }
    }
  }
}

// Styling-x and their alias classes
@mixin typography-theme-map-loop($styling-parent-map) {
  @each $styling-id, $map in $styling-parent-map {
    // .styling-x classes
    @include print-styling-classes($styling-id, $map);
    // alias classes
    @include print-alias-classes($styling-id, $map);
  }
}

// Links
@mixin print-link-styles($link-parent-map) {
  color: setStyle($link-parent-map, color);
  @if (getStyle($link-parent-map, text-decoration)) != null {
    text-decoration: setStyle($link-parent-map, text-decoration);
  }
  @if (getStyle($link-parent-map, border-radius)) != null {
    border-radius: getStyle($link-parent-map, border-radius);
  }

  &.-active, &:active {
    color: setStyle($link-parent-map, states, active, color);
    @if (getStyle($link-parent-map, states, active, background)) {
      background-color: setStyle($link-parent-map, states, active, background);
    }
  }
  &:hover {
    color: setStyle($link-parent-map, states, hover, color);
    @if (getStyle($link-parent-map, states, hover, background)) {
      background-color: setStyle($link-parent-map, states, hover, background);
    }
    @if (getStyle($link-parent-map, states, hover, text-decoration)) {
      text-decoration: setStyle($link-parent-map, states, hover, text-decoration);
    }
  }
}
