// Contains all css rules for element selectors.
// WARNING: Rules here can have unforseen effects. Keep as minimal as possible.
// Set base font size on html for rem calculation
@mixin atRoot-document-styles {
  @at-root {
    html {
      font-size: $base-font-size;
    }
    // set base body font values
    body {
      font-size: $base-mobile-font-size;
      font-family: $base-font-family, $backup-font-family;
      line-height: 1.33;
      color: color("inky-blue");
      @include media($tablet-up) {
        font-size: $base-font-size;
      }
    }
  }
  // end at-root

  // this is part of neat grid/box_sizing and must be included
  // at-root for neat to work in a namespace
  html { // http://bit.ly/1qk2tVR
    box-sizing: border-box;
  }

  * {
    &,
    &::after,
    &::before {
      box-sizing: inherit;
    }
  }
}