@mixin themed-button-base-styles($style-map) {
  // button base styles
  @if getStyle($style-map, min-width) != null {
    min-width: setStyle($style-map, min-width);
  }
  @if getStyle($style-map, margin-top) != null {
    margin-top: setStyle($style-map, margin-top);
  }

  font-family: setStyle($style-map, font-family);
  @if getStyle($style-map, font-size) != null {
    font-size: setStyle($style-map, font-size);
  }
  @if getStyle($style-map, button-line-height) != null {
    line-height: setStyle($style-map, button-line-height);
  }
  @if getStyle($style-map, button-border-radius) != null {
    border-radius: setStyle($style-map, button-border-radius);
  }
  // Overide for link styles
  text-decoration: none;

  @if getStyle($style-map, mobile, width) != null {
    @include media(getStyle($style-map, mobile, breakpoint)) {
      width: getStyle($style-map, mobile, width);
    }
  }
  // set padding top/bottom mobile
  @if getStyle($style-map, mobile, padding-vertical) != null {
    padding-top: setStyle($style-map, mobile, padding-vertical);
    padding-bottom: setStyle($style-map, mobile, padding-vertical);  
  }
  // set padding left/right mobile
  @if getStyle($style-map, mobile, padding-horizontal) != null {
    padding-left: setStyle($style-map, mobile, padding-horizontal);
    padding-right: setStyle($style-map, mobile, padding-horizontal);  
  }
  @if getStyle($style-map, tablet) != null {
    @include media(getStyle($style-map, tablet, breakpoint)) {
      
      @if getStyle($style-map, tablet, line-height) != null {
        line-height: setStyle($style-map, tablet, line-height);
      }

      @if getStyle($style-map, tablet, font-size) != null {
        font-size: setStyle($style-map, tablet, font-size);
      }
      // set padding top/bottom tablet
      padding-top: setStyle($style-map, tablet, padding-vertical);
      padding-bottom: setStyle($style-map, tablet, padding-vertical);
    
      // set padding left/right tablet
      @if getStyle($style-map, tablet, padding-horizontal) != null {
        padding-left: setStyle($style-map, tablet, padding-horizontal);
        padding-right: setStyle($style-map, tablet, padding-horizontal);  
      }
    }
  }
  
  @if getStyle($style-map, desktop) != null {
    @include media(getStyle($style-map, desktop, breakpoint)) {

      @if getStyle($style-map, desktop, line-height) != null {
        line-height: setStyle($style-map, desktop, line-height);
      }
      @if getStyle($style-map, desktop, font-size) != null {
        font-size: setStyle($style-map, desktop, font-size);
      }
      // set padding top/bottom desktop
      padding-top: setStyle($style-map, desktop, padding-vertical);
      padding-bottom: setStyle($style-map, desktop, padding-vertical);
    
      // set padding left/right desktop
      @if getStyle($style-map, desktop, padding-horizontal) != null {
        padding-left: setStyle($style-map, desktop, padding-horizontal);
        padding-right: setStyle($style-map, desktop, padding-horizontal);  
      }
    }
  }

  // DS2 only
  @if getStyle($style-map, icon) != null {
    img,
    i {
      height: setStyle($style-map, icon, max-height);
      line-height: setStyle($style-map, icon, max-height);
      padding-right: setStyle($style-map, icon, padding-horizontal);

      @include media(setStyle($style-map, icon, desktop, breakpoint)) {
        padding-right: getStyle($style-map, icon, desktop, padding-horizontal);
      }
      &.-right {
        float: right;
        padding-left: setStyle($style-map, icon, padding-horizontal);
        padding-right: 0;

        @include media(setStyle($style-map, icon, desktop, breakpoint)) {
          padding-left: getStyle($style-map, icon, desktop, padding-horizontal);
          padding-right: 0;
        }
      }
    }
  }
}

@mixin themed-button-variation-styles($button-type) {

  // Overide for link styles
  text-decoration: none;
  @if themeHasKey($button-type, font-family) {
    font-family: setStyle($button-type, font-family);
  }
  color: setStyle($button-type, button-text-color);
  background-color: setStyle($button-type, button-background-color);

  @if getStyle($button-type, button-border-weight) != null {
    border: setStyle($button-type, button-border-weight) solid setStyle($button-type, button-border-color);
  }
  
  // primary hover
  &:hover,
  &:focus,
  &:active {
    color: setStyle($button-type, hover, button-text-color);
    background-color: setStyle($button-type, hover, button-background-color);
    @if getStyle($button-type, hover, text-decoration) != null {
      text-decoration: setStyle($button-type, hover, text-decoration);
    }
    @if getStyle($button-type, hover, border-color) != null {
      border-color: setStyle($button-type, hover, border-color);
    }
    text-decoration: underline,
  }

  // primary disabled
  &:disabled,
  .-disabled,
  &.-disabled {
    color: setStyle($button-type, disabled, text-color);
    background-color: setStyle($button-type, disabled, button-background-color);
    border-color: setStyle($button-type, disabled, border-color);

    &:hover,
    &:focus,
    &:active {
      background-color: setStyle($button-type, disabled, button-background-color);
    }
  }

  @if themeHasKey($button-type, white) {
    //This style is deprecated as part of STYLGD-8295
    &--white { 
      color: setStyle($button-type, white, text-color);
      border: setStyle($button-type, white, border);
      background-color: setStyle($button-type, white, background-color);

      &:hover,
      &:focus,
      &:active {
        background-color: setStyle($button-type, white, background-color-hover);
        color: setStyle($button-type, white, text-color-hover);
      }
    }
  }
}

@mixin export-theme-button-classes($base-theme) {

  // button base styles
  @include themed-button-base-styles(getStyle($base-theme, base-styles));

  // Output default button under .button class
  @include themed-button-variation-styles(getStyle($base-theme, buttons, button-primary));

  // Output secondary button
  &.-secondary,
  &.-inverted {
    @include themed-button-variation-styles(getStyle($base-theme, buttons, button-secondary));
  }

  &.-tertiary {
    @if getStyle($base-theme, buttons, button-tertiary) != null {
      @include themed-button-variation-styles(getStyle($base-theme, buttons, button-tertiary));
    }
  }
}

// base icon button styles for font sizing and vertical spacing
@mixin button-icon-styles($icon-map) {
  font-size: setStyle($icon-map, font-size);
  padding-right: setStyle($icon-map, padding-horizontal);
  &.-right-icon {
    padding-left: setStyle($icon-map, padding-horizontal);
  }
}
