@mixin divider($border-side, $border-width) {
  width: 100%;
  border: 0;
  border-#{$border-side}: $border-width solid color(graphite);
  padding: 0;
  margin: 9px 0;
}

@mixin divider-small {
  @include divider(top, 1px);
  margin-top: $small-spacing;
  margin-bottom: 19px;

  &.-small-spacing {
    margin-top: $small-spacing;
    margin-bottom: 19px;
  }
  &.-medium-spacing {
    margin-top: $large-spacing;
    margin-bottom: 39px;
  }
  &.-large-spacing {
    margin-top: $xl-spacing;
    margin-bottom: 59px;
  }
}

@mixin divider-variation-1 {
  @include divider-small;
}

@mixin divider-medium {
  @include divider(top, 3px);
  margin-top: 19px;
  margin-bottom: 18px;

  &.-small-spacing {
    margin-top: 19px;
    margin-bottom: 18px;
  }
  &.-medium-spacing {
    margin-top: 39px;
    margin-bottom: 38px;
  }
  &.-large-spacing {
    margin-top: 59px;
    margin-bottom: 58px;
  }
}
@mixin divider-variation-3 {
  @include divider-medium;
}

@mixin divider-variation-2 {
  @include divider-medium;
}

@mixin divider-vertical {
  display: inline-block;
  border: 0;
  padding: 0;
  margin: 0;

  &:before {
    content: '';
    border-left: 3px solid color(graphite);
    position: absolute;
    top: 0;
    bottom: 0;
    display: inline-block;
  }
  &-variation-1 {
    border-left: 1px solid color(graphite);
  }
  &-variation-2 {
    border-left: 2px solid color(graphite);
  }
}

@mixin divider-vertical-wrap {
  position: relative;
  display: inline-block;
}