// HTML ONLY Select Menu [start]
@mixin select-one {
  display: inline;
  position: relative;

  select {
    margin-right: -20px; // offset for icon-chevron-down
    padding-top: 16px;
    padding-right: 20px + $base-mobile-font-size + 5px; // added space for icon-chevron-down + clearance to the left of the icon-chevron-down
    padding-bottom: 16px;
    padding-left: 20px;
    width: 100%;
    border: 1px solid color("graphite-dark"); // border need to be on select itself to support native browser focus outline
    border-radius: 0;
    font-size: $dropdown-base-font-size;
    line-height: 16px; // for total height = 50px
    background-color: #fff;
    //text-overflow: ellipsis; // does not work on select elements in Chrome and IE, only Firefox.
    cursor: pointer;
    -webkit-appearance: none; // remove browser default select arrow
    -moz-appearance: none; // remove browser default select arrow

    &:focus {
      border-color: color("interactive-blue");
    }
  }

  @include media($tablet-up) {
    select {
      padding-top: 20px;
      padding-right: 20px + $base-font-size + 5px; // added space for icon-chevron-down + clearance to the left of the icon-chevron-down
      padding-bottom: 20px;
      width: auto;
      font-size: $base-font-size;
      line-height: 18px; // for total height = 60px
    }
  }

  .icon-chevron-down[aria-hidden="true"] {
    position: absolute;
    top: 0;
    right: 0;
    pointer-events: none;
  }

  .icon-chevron-down[aria-hidden="true"]:before {
    color: color("kp-blue");
  }

  select:disabled {
    color: $select-menu-disable-color;
    border-color: $select-menu-disable-color;
    cursor: not-allowed;
  }

  select.error-field + .icon-chevron-down[aria-hidden="true"]:before,
  select:disabled + .icon-chevron-down[aria-hidden="true"]:before {
    color: color("dolphin");
  }

  select::-ms-expand { // remove IE browser default select arrow
    display: none;
  }

  select:disabled::-ms-value {
    color: $select-menu-disable-color;
  }
}

.select-one {
  @include select-one;
}
// HTML ONLY Select Menu [end]


// JavaScript Dropdown [start]
// This implementation is being phase out and should not be used for new features.
// Retain the following for backward compatibility.
$dropdown-option-menu-max-width: 300px;
$dropdown-min-mobile-height: $small-spacing;
$dropdown-chevron-size: $small-font-size;
$tiny-spacing-minus-border: $tiny-spacing - 1;
$input_height--large: 60px;
$input_height--small: 50px;
//Mixins
@mixin select-dropdown {
  @extend %form-control;
  @include size(100% $input_height--small);
  background: white;
  line-height: 1.5;
  padding: $tiny-spacing-minus-border;
  position: relative;
  @include media($tablet) {
    max-width: $dropdown-option-menu-max-width;
    height: $input_height--large;
    line-height: 2;
  }
  @include media($desktop) {
    line-height: 1;
    height: $input_height--large;
    padding: 22px $tiny-spacing-minus-border;
    max-width: $dropdown-option-menu-max-width;
  }
  label {
    cursor: pointer;
    display: block;
  }
  &.focus {
    border-color: $link-color;
    box-shadow: 0 0 0 1px $link-color, 0 1px 0 1px #ddd;
  }
  &.focus::after {
    border-top-color: #709AD0;
  }
  &_disabled {
    .-disabled-content {
      color: color("graphite-dark");
    }
  }
}

@mixin dropdown-overlay {
  @include size(100% auto);
  padding: 5px;
  &:hover {
    border-color: #aaa;
    border-bottom-color: #999
  }
}

@mixin dropdown-icon {
  color: $link-color;
  font-size: $dropdown-chevron-size;
  margin-left: $tiny-spacing;
}

@mixin label--hide-default {
  visibility: hidden;
  opacity: 0;
  height: 0;
}

.select-dropdown {
  @include select-dropdown;
  & .icon-chevron-down {
    float: right;
    color: $link-color;
  }
  .label__select-form--hidden {
    @include label--hide-default;
  }
  .label__proxy-picker--hidden {
    @include label--hide-default;
  }
  .dropdown__form-overlay {
    @include dropdown-overlay;
  }
  .proxy-picker-overlay {
    @include dropdown-overlay;
    .icon-chevron-down {
      @include dropdown-icon;
    }
    .value__selected {
      @include styling(3);
      color: $link-color;
    }
  }
  // hide and overlay when JavaScript is enabled
  select {
    @include position(absolute, 0 null null 0);
    opacity: 0;
    @include size(100%);
    cursor: pointer;
    display: block;
    -webkit-appearance: none;
  }
}
.select-dropdown_disabled:focus{
  border-color: color("graphite-dark");
}
// JavaScript Dropdown [end]