@import "type-lock-ups-lib";
.short__data__point {
  @include data-points;
  @include phone-number-styling;
}

.long__data__point {
  @include data-points;
  @include phone-number-styling;
}

.listed__data__points {
  margin-bottom: 28px;
  @include media($mobile) {
    margin-bottom: 20px;
  }
  span, a {
    @include media($mobile) {
      @include styling(7);
    }
  }
}

hr.reviewed__by--divider {
  margin-bottom: 38px;
  @include media($mobile) {
      margin-bottom: 18px;
  }
}
.reviewed__by--title, .reviewed__by--link, .reviewed__by--copyright {
  display: block;
  @include media($mobile) {
    @include styling(9);
  }
}
.reviewed__by--link {
  margin-bottom: 20px;
}
