// Last updated: 2020-08-06 by Lauren Maxwell
@include ms-clear; // remove Microsoft Edge default clear 'X' button

@mixin form-messages {
  @include styling(8);
  & p {
    margin: $extra-tiny-spacing auto;
  }
}

@mixin text-area_global {
  @include styling(6);
  display: block;
  padding: $tiny-spacing;
  width: calc(100% - 28px);
  height: 252px;
  border: 1px solid $text-area-border-color;
  border-radius: 0;
  color: $label-color;
  -webkit-appearance: none;
  &.focus {
    border-color: $link-color;
  }
  &[data-show-lines='1'] {
    height: 54px;
  }
  &[data-show-lines='2'] {
    height: 76px;
  }
  &[data-show-lines='3'] {
    height: 98px;
  }
  &[data-show-lines='4'] {
    height: 120px;
  }
  &[data-show-lines='5'] {
    height: 142px;
  }
  &[data-show-lines='6'] {
    height: 164px;
  }
  &[data-show-lines='7'] {
    height: 186px;
  }
  &[data-show-lines='8'] {
    height: 208px;
  }
  &[data-show-lines='9'] {
    height: 230px;
  }
  &[data-show-lines='10'] {
    height: 252px;
  }
  @include media($mobile){
    @include styling(7);
    height: 234px;
    &[data-show-lines='1'] {
      height: 54px;
    }
    &[data-show-lines='2'] {
      height: 74px;
    }
    &[data-show-lines='3'] {
      height: 94px;
    }
    &[data-show-lines='4'] {
      height: 114px;
    }
    &[data-show-lines='5'] {
      height: 134px;
    }
    &[data-show-lines='6'] {
      height: 154px;
    }
    &[data-show-lines='7'] {
      height: 174px;
    }
    &[data-show-lines='8'] {
      height: 194px;
    }
    &[data-show-lines='9'] {
      height: 214px;
    }
    &[data-show-lines='10'] {
      height: 234px;
    }
  }
}

// Form Control
// move form control extend from modern to lib file
%form-control-base {
  padding: 20px;
  border: solid $input-border-width $input-grey-color;
  vertical-align: middle;
  color: $text-dark;
  &:focus {
    border-color: $input-active-border-color;
  }
  &::-webkit-input-placeholder {
    color: $placeholder-text-color;
  }
  &:-moz-placeholder {
    color: $placeholder-text-color;
  }
}

@mixin form-control {
  @extend %form-control-base;
}

// Differences are the blocked @at-root declarations - not sure why this is needed but keeping it in
%form-control {
  @extend %form-control-base;
  @at-root #{&}::-webkit-input-placeholder {
    color: $placeholder-text-color;
  }
  @at-root #{&}:-moz-placeholder {
    color: $placeholder-text-color;
  }
}

// Form group
%form-group {
  input {
    @extend %form-control;
    @include media($desktop) {
      max-height: $input_height--large;
      width: auto;
    }
    @include media($mobile) {
      max-height: $input_height--small;
      width: 100%;
    }
  }

  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="url"],
  textarea {
    -webkit-appearance: none; 
    border-radius: 0;
  }

  label {
    @include styling(8);
    color: $label-color;
    display: block;
    margin-bottom: $extra-tiny-spacing;
    text-transform: $label-font-style;
  }

  .form-textbox {
    position: relative;
    input {
      padding-right: 32px;
      width: 236px;
    }
  }
}

@mixin form-group {
  @extend %form-group;
}

// Fieldset
%fieldset{
  margin: 0;
  padding: 0;
  border: none;
  legend{
    @include styling(6);
    font-family: $medium-font-family;
    color: $text-dark;
    padding-bottom: $extra-tiny-spacing;
  }
}

// Disabled Field
%disabled-field {
  border: solid 1px $disabled-color-forms;
  background-color: $disabled-color-forms;
  cursor: not-allowed !important;
}

@mixin disabled-field {
  @extend %disabled-field;
}

//Errors
%error-input {
  input {
    background-color: rgba(182, 49, 37, 0.05);
    border: 1px solid $error-color;
  }
}

%error {
  color: $error-color;
  @extend %error-input;
  .form-textbox {
    @extend %error-input;
  }
}