@import "kp-icons-lib";
@import "icons-lib";
@import "buttons-theming";

// @debug $button-vars-ds;

@mixin -block {
  display: block;
  width: 100%;
  min-width: 100%;
}

@mixin rounded-input {
  @include cross-browser("appearance", "none");
  display: inline-block;
  min-width: $button-min-width;
  font-weight: normal;
  cursor: pointer;
  border: none;
  user-select: none;
  text-align: center;
  height: auto;
  line-height: 20px;
  vertical-align: middle;
  white-space: normal;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  @include media($tablet-up) {
    line-height: 22px;
  }
}

@mixin button-($position) {
  text-align: $position;
}

@mixin button_position($position) {
  float: $position;
}

// This mixin is depreciated as of SG 5.0
@mixin button {
  @include rounded-input;
  @include cross-browser("transition", "background-color 0.2s ease,\a   color 0.2s ease,\a   border-color 0.2s ease");
  border: $button-border-size transparent solid;
  &:hover,
  &:focus,
  &:active {
    color: white;
  }
  &.-action {
    background-color: transparent;
    border-color: white;
    &:hover,
    &:focus {
      background-color: white;
      color: $button-hover-color;
    }
    &.-inverted {
      border-color: white;
      color: $button-color;
      &:hover,
      &:focus {
        background-color: white;
        color: $button-hover-color;
      }
    }
  }
  &.-inverted {
    &:hover,
    &:focus,
    &:active {
      border-color: $button-hover-color;
    }
    &:disabled,
    .-disabled,
    &.-disabled {
      background-color: $button-inverted-disable-color;
    }
  }
  &.-small {
    min-width: $button-small-min-width;
    font-size: $tiny-font-size;
  }
  &:disabled,
  .-disabled {
    &:hover {
      cursor: not-allowed;
      border-color: $button-disable-color;
    }
    &.-inverted {
      color: $button-disable-color;
      border: $button-border-size solid $button-disable-color;
    }
  }
  &.-left {
    @include button_position(left);
  }
  //float center attribute does not exist 
  // &.-center {
  //   @include button_position(center);
  // }
  &.-right {
    @include button_position(right);
  }
  &.-tertiary {
    background: none;
    min-width: auto;
    border: none;
    color: $link-color;
    padding: 0;
    height: auto;
    margin: 14px  0;
    @include media($tablet-up){
      margin: 19px 0;
    }
    &:hover,
    &:focus,
    &:active {
      color: $link-hover-color;
    }
  }
  @include media($mobile) {
    width: 100%;
    & + & {
      margin-top: $tiny-spacing;
    }
  }
}

@mixin button-unstyled {
  @include cross-browser("appearance", "none");
  color: $button-color;
  border: 0 solid transparent;
  background-color: transparent;
  text-align: left;
  padding: 0;
}

@mixin -close-styles {
  color: $button-color;
  font-size: $small-font-size;
  border: none;
  background: none;
}

@mixin -close {
  @include -close-styles;
  @include media($device) {
    font-size: $small-font-size;
  }
  &--large {
    @include -close-styles;
    font-size: $large-font-size;
  }
  &--small {
    @include -close-styles;
    font-size: $small-font-size;
  }
}

// DS2
@mixin button__icon--left {
  padding-left: $ds2-button__padding-horizontal--icon-side;
  .icon {
    position: absolute;
    top: $ds2-button__icon--position-vertical;
    margin-top: $ds2-button__icon--position-vertical-offset;
    left: $ds2-button__icon--position-horizontal;
  }
}
// DS2
@mixin button__icon--right {
  padding-right: $ds2-button__padding-horizontal--icon-side;
  .icon {
    position: absolute;
    top: $ds2-button__icon--position-vertical;
    margin-top: $ds2-button__icon--position-vertical-offset;
    right: $ds2-button__icon--position-horizontal;
  }
}
// DS2
@mixin button__icon--only {
  line-height: 0;
  padding: $ds2-button__padding-vertical--mobile;

  @include media($tablet){
    padding: $ds2-button__padding-vertical--tablet;
  }
  @include media($desktop){
    padding: $ds2-button__padding-vertical--desktop;
  }
}

%rounded-input {
  @include cross-browser("appearance", "none");
  display: inline-block;
  cursor: pointer;
  user-select: none;
  text-align: center;
  height: auto;
  vertical-align: middle;
  white-space: normal;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
}

%button {
  @extend %rounded-input;

  // Todo add font styles
  @include cross-browser("transition", "background-color 0.2s ease,\a   color 0.2s ease,\a   border-color 0.2s ease");

  &.-large {
    font-size: $ds2-button__large-font-size;
    line-height: $ds2-button__large-line-height;
  }
  &.-small {
    min-width: $button-small-min-width;
    font-size: $tiny-font-size;
  }
  &.-left {
    @include button_position(left);
  }
  &.-right {
    @include button_position(right);
  }
  // DS1/DS2 -- Tertiary shares the same styles across DS1/DS2
  &.-tertiary {
    color: $link-color;
    background: transparent;
    border: 0 none;
    border-radius: 0;
    min-width: auto;
    padding: 0;
    height: auto;
    margin: 14px 0;
    @include media($tablet-up) {
      margin: 19px 0;
    }
    &:hover,
    &:focus,
    &:active {
      color: $link-hover-color;
      background: transparent;
    }
  }
  @include media($mobile) {
    & + & {
      margin-top: $tiny-spacing;
    }
  }
  // DS2 - new
  &.button__icon--left {
    @include button__icon--left;
  }

  // DS2 - new
  &.button__icon--right {
    @include button__icon--right;
  }

  // DS2 - new
  &.button__icon--only {
    @include button__icon--only;
  }

  // DS2 Supported Only -- Front Door button styles
  &.button__icon--left,
  &.button__icon--right,
  &.button__icon--only {
    &:hover,
    // &:focus - do not define focus state, use browser default outline
    &:active {
      img {
        filter: $ds2-button__icon-svg-filter--default;
      }
    }
  }
}

%-close-icon {
  &:before {
    @include icon-for("close");
  }
}

%-button-icon {
  & {
    @include button-icon-styles(getStyle($button-vars-ds, base-styles, icon));
  }
  color: inherit;
  overflow: hidden;
  padding-right: $small-spacing;
  vertical-align: middle;
  display: inline-block;
  &.-right-icon {
    padding-right: 0;
  }
}

%button-unstyled {
  @include cross-browser("appearance", "none");
  color: $link-color;
  border: 0px solid transparent;
  background-color: transparent;
  text-align: left;
  padding: 0;
}

%-close {
  color: $button-color;
  font-size: $medium-font-size;
  border: none;
  background: none;
}

%-primary-action {
  @include media($device) {
    width: 100%;
    display: block;
    white-space: normal;
    word-wrap: break-word;
    line-height: 22px;
  }
}
