// Last updated: Version 4.0 on 2020-01-15
$defaultfont: sans-serif;
$webview-weight-array: (light: 200, book: 400, medium: 600);

.webview-content {
  display: block;
  /* stylelint-disable declaration-no-important */
  font-family: $defaultfont !important;
  /* stylelint-enable */

  .hide-webview-content {
    display: none;
  }

  b, strong {
    font-weight: bold;
    font-family: inherit;
  }

  // match this section with typography.scss
  @each $weight-name,
    $weight-value in $webview-weight-array {
    // single-dash is preferred, but will support double-dash for legacy implementations
    .-#{$weight-name},
    .\--#{$weight-name} { // escpaed double hyphen for IE and legacy mobile Safari suport
      font-family: $defaultfont;
      font-weight: $weight-value;
    }
  }
}
