// Generated by grunt-webfont
@import "kp-icons-lib";

@at-root {
  @font-face {
    @include font-face;
  }
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  @extend %icon-class;
}

@each $icon-name,
  $icon-value in $kp-icons {
    .icon-#{$icon-name}:before {
      @include icon-content-output($icon-name)
    }
  }
