// document.scss
@import "document-lib";

// Set bold and strong tags to use medium font family
b,
strong {
  font-weight: normal;
  font-family: $medium-font-family;
}

// Base link coloring / Base link theming 
// pulled from typography-themeing.scss
// through _document-lib.scss
a {
  // get default theme toggle
  @include print-link-styles(getStyle($typography-vars-ds, base-typography, link));

  @if getStyle($typography-vars-ds, base-typography, link, inverted) {
    &.inverted, &.-inverted {
      @include print-link-styles(getStyle($typography-vars-ds, base-typography, link, inverted));
    }
  }
}

// Prevent iOS auto zoom
@include media($device) {
  input:not([type="button"]):not([type="checkbox"]):not([type="radio"]),
  select,
  textarea {
    font-size: 16px;
  }
}