// SHARED 
// Base main files - Styles that do not get scoped to ds1 or ds2

// Loads all styleguide files, besides third-party dependencies.
@import "variables";

@import "colors";
@import "color-aliases";
@import "helper-mixins";

// Pull in responsive variables and responsive breakpoints
@import "variables/breakpoint.var.ds1"; // sets $grid-vars
@import 'document-base'; // at root base body typography

@import 'typography-base'; // Base typography styles font and weight
@import 'links';

@import "accessibility";

* {
  box-sizing: border-box;
}

@each $name, $bgcolor in $colors {
  .#{$name} {
    background-color: $bgcolor;
    color: set-text-color($bgcolor);
    &.-inverted {
      background-color: color(graphite-light);
      color: $bgcolor;
    }
  }
}
@import "webview";
@import "print";
@import "version-number";
@import "scrollbox";
