@mixin region-picker {
  .regionPicker {
    position: relative;
    font-size: $tiny-font-px;
    color: color(inky-blue);
    @include media($device) {
      font-size: $small-font-px;
    }
    @include media($tablet) {
      font-size: $small-font-px;
    }
    .icon-chevron-down {
      float: none;
      pointer-events: none;
      color: color(kp-blue);
      @include media($device) {
        margin-left: 4px;
        right: 0;
      }
      &:before {
        vertical-align: top;
        margin-left: 4px;
      }
    }
    .navigational-select-dropdown {
      display: inline-block;
      border: 0 none;
      margin: $small-spacing 0;
      padding: 0;
      min-height: auto;
      height: auto;
      width: auto;
      min-width: auto;
      background-color: transparent;
      -webkit-appearance: none;
      -moz-appearance: none;
      @include media($device) {
        margin: $medium-spacing 0;
      }
      @include media($tablet) {
        width: auto;
      }
      @include media($desktop) {
        width: auto;
      }
      &:focus {
        outline: 1px dotted #212121;
        outline: 5px auto -webkit-focus-ring-color;
        @include firefox-tab-focus-buttons;
      }
    }
    .screenReader {
      display: block;
    }
    .navigational-select-dropdown__list {
      $z-index-foreground: 200;
      background-color: $white;
      position: absolute;
      right: 0px;
      left: 0;
      top: 75%;
      display: none;
      opacity: 0;
      z-index: $z-index-foreground;
      font-size: inherit;
      border: 1px solid color(graphite);
      text-align: left;
      padding: 0;
      min-width: 31.5em;
      @include media($device) {
        min-width: $full-width;
      }
      @include media($tablet) {
        min-width: $full-width;
      }
      @include media($mobile) {
        min-width: $full-width;
      }
      &.-expanded {
        display: block;
        opacity: 1;
      }
      .navigational-select-value {
        width: $full-width;
        list-style: none;
        padding: 15px;
        cursor: pointer;
        float: left;
        border-bottom: 1px solid #E9EBEE;
        & :last-child{
          border-bottom: none;
        }
        & a {
          color: color('inky-blue');
        }
        &:hover {
          background-color: color(kp-blue);
          & a {
            color: $white;
          }
        }
      }
      &.focus {
        border: 0 none;
        box-shadow: 0 0;
        .select-value {
          outline: 1px dotted $text-dark;
          outline-offset: 3px;
        }
      }
      .navigational-dropdown-overlay, select {
        @include media($mobile) {
          width: $full-width;
        }
      }
      &.-white-bckgrnd {
        background-color: $white;
        select {
          border: 0 none;
        }
      }
    }
  }
}
