$line-height-difference--large: 14px;
$line-height-difference--medium: 10px;

$locked: false;

@mixin caps {
  text-transform: uppercase;
}


@function px-to-rem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@function px-to-em($pixels, $context: $base-font-size) {
  @return #{$pixels/$context}em;
}

//Mixin for size
@mixin calculateRem($size) {
  font-size-pixel: $size; // fallback in px
  font-size-rem: calculateRem($size);
}

//Mixin for JKP projects
@function large-line-height($line-height) {
  @return $line-height-difference--large - $font-size;
}

@function medium-line-height($line-height) {
  @return $line-height-difference--medium + $font-size;
}

@function small-line-height($line-height) {
  @return $line-height / $font-size;
}

