//Refernced in link style sheet
@mixin anchor-styles {

  &.divider-link {
    padding-right: $tiny-spacing;
    border-right: 1px solid color('graphite-dark');
    margin-right: $extra-tiny-spacing;

    &:last-of-type {
      padding-right: 0;
      border-right: 0;
    }
  }
  
  // Override for icons inside of links
  // so text underline does not apply to icons
  i:before {
    text-decoration: none;
    display: inline-block;
  }

  &.button {
    height: auto;

    &:hover {
      text-decoration: none;
    }
  }

  &.external-link {
    &.button i {
      font-size: 10px;
      padding: 0 5px;
      position: relative;
      bottom: 5px;
    }
  }
}

@mixin icon-link-out {
  color: $link-color;
  padding-left: 5px;
  font-size: 8px;
  position: relative;
  bottom: 5px;

  &.extlink {
    font-size: 9.8px;
    bottom: 3px;
    padding-left: 4px;

    &:before {
      font-weight: bold;
    }
  }
}

@mixin new-window-link {
  .icon-zz009new-window {
    @include size(14px);
    padding-left: 5px;
    vertical-align: super;
    &:before {
      @include size(14px);
      font-size: 14px;
      display: inline-block;
    }
  }
}

@mixin back-link { 
  ul {
    list-style-type: none;
    @extend %list-unstyled;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    padding-inline-start: 0;
  }
}