@import "buttons-lib";

.button {
  // Depreciated?
  &.-action {
    background-color: transparent;
    border-color: white;
    &:hover,
    &:focus {
      background-color: white;
      color: $button-hover-color;
    }
    &.-inverted {
      border-color: white;
      color: $button-color;
      &:hover,
      &:focus {
        background-color: white;
        color: $button-hover-color;
      }
    }
  }
  &.-inverted {
    // This style is deprecated as part of STYLGD-8295
    &--white {
      color: $button-color--white;
      border: $button-border-size solid $button-color--white;
      background-color: transparent;
      &:hover,
      &:focus,
      &:active {
        background-color: $button-color--white;
        color: $button-color;
      }
    }
  }
  
  &:disabled,
  .-disabled {
    &:hover {
      cursor: not-allowed;
      border-color: $button-disable-color;
    }
    &.-inverted {
      color: $button-disable-color;
      border: $button-border-size solid $button-disable-color;
    }
  }
}

// Theming scoped cascade override
button.-close {
  &--large {
    font-size: $large-font-size;
  }
}

.-close {
  @extend %-close;
  @extend %-close-icon;

  @include media($device) {
    font-size: $small-font-size;
  }

  &--large {
    @extend %-close;
    @extend %-close-icon;
    font-size: $large-font-size;
  }

  &--small {
    @extend %-close;
    @extend %-close-icon;
    font-size: $small-font-size;
  }
}

.-primary-action {
  @extend %-primary-action;
}
