@import "ratings-lib";
.star_3-img{
  height: 32px;
}
.star_img{
  height: 18px;
}

@each $name, $star-rating-value in $star-rating-array {
  .star-rating_#{$name} {
    @at-root #{&}-large {
      &:before {
        content: $star-rating-value;
        @include star-styles(32px);
      }
      &-img{
        width: 34px;
        height: 36px;
        float: left;
        object-fit: cover;
        max-width: 100%;
        max-height: 100%;
        display: block;
      }
    }

    @at-root #{&}:before {
      content: $star-rating-value;
      @include star-styles(18px);
    }
    @at-root #{&}-img {
      width: 20px;
      height: 20px;
      float: left;
    }
  }
}

.star-rating_text {
  @at-root #{&}-inline {
    @include styling(5);
    font-size: $small-font-size;
    display: inline-block;
    padding: $extra-tiny-spacing;
    margin: 0;
  }

  @at-root #{&}-inline_large {
    @include styling(3);
    // font-size: $large-font-size;
    display: inline-block;
    padding: $extra-tiny-spacing;
    margin: 0;
  }

  @at-root #{&}-summary {
    @include styling(4);
    margin: 0;
    display: inline-block;
  }
}

.star-rating_variation1,
.star-rating_variation2 {
  .star-rating_text {
    &-inline {
      padding: 0 0 3px 0;
      @include styling(5);
    }
    &-summary {
      margin: 0;
      @include styling(6);
      display: block;
    }
  }
}

.star-rating_variation1 {
  padding-top: $medium-spacing;
  padding-bottom: $medium-spacing;

  .star-rating_text {
    @at-root #{&}-inline {
      padding: 0 0 3px 0;
    }
  }
  .comments {
    padding-left: 14px;
  }
}

.star-rating_variation2 {
  padding-bottom: 10px;
  .star-rating_text {
    @at-root #{&}-inline {
      padding: 0 0 0 3px;
    }
    @at-root #{&}-summary {
      padding-bottom: 3px;
    }
  }
}
