

// Add theming ability to lib files.
// typography map is inherited to this file depending on theme
// styling(x) should work with theme option set
$default-typography-map: $typography-vars-ds;

// Functions
@function px-to-rem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@function px-to-em($pixels, $context: $base-font-size) {
  @return #{$pixels/$context}em;
}

//Mixin for JKP projects
@function large-line-height($line-height) {
  @return $line-height-difference--large - $font-size;
}

@function medium-line-height($line-height) {
  @return $line-height-difference--medium + $font-size;
}

@function small-line-height($line-height) {
  @return $line-height / $font-size;
}

@mixin caps {
  text-transform: uppercase;
}

@mixin muted {
  color: darken(color(graphite-dark), 20%);
}

@each $color-name,
  $color-value in $color-array {
  // single-dash is preferred, but will support double-dash for legacy implementations
  %-#{$color-name},
  %--#{$color-name} { // escapes double hyphen for IE and legacy mobile Safari suport
    color: $color-value;
  }
}

@each $weight-name,
  $weight-value in $weight-array {
  // single-dash is preferred, but will support double-dash for legacy implementations
  %-#{$weight-name},
  %--#{$weight-name} { // escapes double hyphen for IE and legacy mobile Safari suport
    font-family: $weight-value;
  }
}

@mixin inverted-link {
  &,
  &:visited {
    color: $text-light;
  }
  &:hover,
  &:focus {
    color: color(interactive-blue);
  }
}
// DS2
@mixin styling-x($sass-map) {
  // @if getStyle($sass-map, )
  @if getStyle($sass-map, font-family) {
    font-family: setStyle($sass-map, font-family);
  }
  @if getStyle($sass-map, font-weight) {
    font-weight: setStyle($sass-map, font-weight);
  }
  font-size: setStyle($sass-map, font-size);
  font-weight: normal;
  line-height: setStyle($sass-map, line-height);
  
  @if getStyle($sass-map, letter-spacing) != null {
    letter-spacing: setStyle($sass-map, letter-spacing);
  }

  @if getStyle($sass-map, caps) == true { // DS1 only
    &%-caps,
    &%--caps,
    &.\-caps,
    &.\--caps { // escapes double hyphen for IE and legacy mobile Safari suport
      @include caps;
    }
  }

  @each $weight-name,
  $weight-value in $weight-array {
    // single-dash is preferred, but will support double-dash for legacy implementations
    &.-#{$weight-name},
    &.\--#{$weight-name} { // escpaed double hyphen for IE and legacy mobile Safari suport
      font-family: $weight-value;
    }
  }

  // DS1 only
  @if getStyle($sass-map, mobile) != null {  
    @include media($mobile) {
      font-size: getStyle($sass-map, mobile, font-size);
      line-height:  getStyle($sass-map, mobile, line-height); 
    }
  }
}

// DS2
// Provide legacy support for default/ds2 theming
@mixin styling($i) {
  @if $default-typography-map != null {
    @each $styling-id, $map in getStyle($default-typography-map, styling) {
      @if ($i == $styling-id) {
        @include styling-x($map);
      }
    }
  }
}

// deprcated, but here in case teams using this mixin
@mixin styling-marketing($i) {
  @include styling($i)
}

%inverted-link {
  &,
  &:visited {
    color: $text-light;
  }
  &:hover,
  &:focus {
    color: color(interactive-blue);
  }
}

//Mixin for size
@mixin calculateRem($size) {
  font-size-pixel: $size; // fallback in px
  font-size-rem: calculateRem($size);
}