$radio-button-font-variation-2: 20px;
$radio-button-size-variation-2: 18px;
$radio-button-position-variation-2: 4px;
$radio-button-space-right-variation-2: 15px;
$radio-button-space-bottom-variation-2: 20px;
$radio-button-font-variation-3: 28px;
$radio-button-size-variation-3: 26px;
$radio-button-position-variation-3: 5px;
$radio-button-space-right-variation-3: 15px;
$radio-button-space-bottom-variation-3: 20px;

@import "radio-buttons-lib";

.radio-button {
  @include radio-button();

  &.-variation-1 {
    @include radio-button($radio-button-font-variation-1, $radio-button-size-variation-1, $radio-button-position-variation-1, $radio-button-space-right-variation-1, $radio-button-space-bottom-variation-1);
  }

  &.-variation-2 {
    @include radio-button($radio-button-font-variation-2, $radio-button-size-variation-2, $radio-button-position-variation-2, $radio-button-space-right-variation-2, $radio-button-space-bottom-variation-2);
  }

  &.-variation-3 {
    @include radio-button($radio-button-font-variation-3, $radio-button-size-variation-3, $radio-button-position-variation-3, $radio-button-space-right-variation-3, $radio-button-space-bottom-variation-3);
  }
}
