$responsive-map: $breakpoint-vars-ds;

@mixin print-responsive-classes($sass-map) {
  @each $styling-id, $map in getStyle($sass-map, breakpoints) {
    .#{$styling-id} {
      @include media(getStyle($map, breakpoint)) {
        display: none !important;
      }
    }
  }
}

@mixin device-x($breakpoint, $responsive-map: $responsive-map) {
  @each $styling-id, $map in getStyle($responsive-map, breakpoints) {
    @if $breakpoint == $styling-id {
      @include media(getStyle($map, breakpoint)) {
        display: none !important;
      }
    }
  }
}

@mixin desktop-only {
  @include device-x('desktop-only');
}

@mixin tablet-up-only {
  @include device-x('tablet-up-only');
}

@mixin device-only {
  @include device-x('device-only');
}

@mixin mobile-only {
  @include device-x('mobile-only');
}
